* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  margin: 0;
  font-family: Roboto, Arial, Helvetica, sans-serif;
}

ul {
  list-style: none;
}

.card {
  box-sizing: border-box;
  background: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 10px;
  flex-direction: column;
  width: 200px;
  padding: 32px 16px 16px;
  transition: all .3s;
  display: flex;
}

.card__image {
  align-self: center;
  width: 160px;
  height: 134px;
  margin-bottom: 40px;
}

.card__description {
  color: #060b35;
  margin: 0 0 4px;
  padding: 0;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__description__:hover {
  color: #34568b;
  transition: color .3s;
}

.card__product-code {
  color: #616070;
  margin: 0 0 16px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__rating {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 24px;
  display: flex;
}

.card__rating-count {
  color: #616070;
  margin: 0;
  font-size: 10px;
  font-weight: 400;
}

.card__price {
  color: #616070;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 16px;
  display: flex;
}

.card__price-text {
  text-align: left;
  color: #060b35;
  margin: 0;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.card__price-num {
  text-align: right;
  color: #060b35;
  height: 18px;
  margin: 0;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__buy-button {
  text-align: center;
  text-transform: uppercase;
  color: #fff;
  background: #00acdc;
  border-radius: 5px;
  width: 100%;
  height: 40px;
  font-size: 14px;
  font-weight: 700;
  line-height: 40px;
  text-decoration: none;
  transition: all .3s;
  display: inline-block;
}

.card__buy-button:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
  font-weight: 700;
}

.stars {
  justify-content: center;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.stars__star {
  background: url("star.822d561e.svg") center no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
  display: flex;
}

.stars--4 > :nth-child(-n+4) {
  background: url("star-active.edda4c43.svg") center no-repeat;
  width: 16px;
  height: 16px;
}
